import {createSelector} from 'reselect';

const getUsersSelector =(state)=>{
    return state.usersPage.users
}

export const getUser = createSelector(getUsersSelector,(users)=>{

    return users.filter(u=> true)
})

export const getPageSize =(state)=>{
    return state.usersPage.pageSize
}

export const getTotalUsersCount =(state)=>{
    return state.usersPage.totalItemsCount
}

export const getCurrentPage =(state)=>{
    return state.usersPage.currentPage
}

export const getIsFetching =(state)=>{
    return state.usersPage.isFetching
}

export const getFollowed =(state)=>{
    return state.usersPage.users.followed
}

export const getFollowingIIsProgress =(state)=>{
    return state.usersPage.followingIsProgress
}

export const getportionSize=(state)=>{
    return state.usersPage.portionSize
}
